.home-hero {
	min-height: 275px;
	width: 100%;
	border-bottom: 4px solid #dc3545;
}
.hero-row {
	background:#fff;
	border:2px solid #ccc;
	@include breakpoint (md) {
		margin-top: 3em;
	}
}
.parallax-cta {
	width: 100%;
	height: 500px;
	text-shadow: 2px 2px #333;
	background: #222;
	background-position: bottom center;
	background-repeat: no-repeat;
	background-size: cover;
	background-attachment: fixed;
	border-bottom: 4px solid blue;
}
.testimonials {
	width: 100%;
	min-height: 150px;
}
.customer-testimonials {
	background: #fff;
}
/* TESTIMONIAL OVERRIDES */
	.hms-testimonial-container > .author {
		font-weight: bold;
		font-size: 1.5rem;
		padding-left: 1.5rem;
	}
	.hms-testimonial-container > .testimonial {
		text-transform: italic;
		font-size: 1.2rem;
		font-style: italic;
		padding: 1.5rem;
	}
/* == */
.footer {
	background: #e9e9e9;
	min-height: 350px;
	width: 100%;
	border-top: 4px solid blue;
}
.featured-prop {
	min-height: 350px;
	width: 100%;
	background-position: middle middle;
	background-repeat: no-repeat;
	background-size: cover;
	background-attachment: fixed;
	border-top: 4px solid #dc3545;
}
.helpful-links {
	background: #e3f2fd;
	width: 100%;
	min-height: 300px;
}



.responsive-text {
	@include breakpoint (sm) {
		font-size: 1.5vw;
	}
	@include breakpoint (md) {
		font-size: 1.6vw;
	}
	@include breakpoint (lg) {
		font-size: 2.2vw;
	}
	@include breakpoint (xl) {
		font-size: 3vw;
	}
}